:root {
  --largeur: 300px;
}

.main_wrapper {
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 20px 0px;
          box-shadow: 0px 0px 20px 0px;
  display: grid;
  grid-auto-flow: row;
  grid-template-rows: 80px auto;
  max-width: var(--largeur);
  margin: 25vh auto;
  overflow: hidden;
}


